 
  .spinner-container {
    z-index: 999;
    width: '100%';
    height: '100%';
    position: 'absolute';
    display: 'flex';
    background-color: '#80808080'
  }

  .result {
    background-color: green;
    color: white;
    padding: 1rem;
    margin-top: 0.8rem;
    border-radius: 1rem;

  }
  .restart{
    margin-top: 1rem;
    margin-left: 1rem;
    background-color: blue;
    border-radius: 0.3rem;
    border-color: grey;
    color: white;
  }